$table-border-color: #999;
$cell-border-color: #eee;
.ng-table {
  th {
    text-align: center;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    button.asc-desc-container {
      display: none;
    }
    &.sortable {
      cursor: pointer;
      .sort-indicator {
        padding-right: 18px;
        position: relative;
      }
      &:hover button span {
        opacity: 1;
      }
      button {
        border: none;
        background: none;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        font-size: .75em;
        &:hover span {
          opacity: 1;
        }
        span {
          opacity: .3;
        }
      }
      &.sort-desc,
      &.sort-asc {
        button span {
          opacity: 1;
        }
      }
      &.sort-desc button {
        span.glyphicon-triangle-top {
          display: none;
        }
        span.glyphicon-triangle-bottom {
          right: 0;
        }
      }
      &.sort-asc button span.glyphicon-triangle-bottom {
        display: none;
      }
      &.sort-desc,
      &.sort-asc {
        background-color: rgba(141, 192, 219, 0.25);
        text-shadow: 0 1px 1px rgba(255, 255, 255, 0.75);
