    }
    &.filter {
      .input-filter {
        margin: 0;
        display: block;
        width: 100%;
        min-height: 30px;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
      }
    }
  }
  .ng-table-group-header {
    th {
      text-align: left;
    }
  }
  .ng-table-group-selector {
    display: block;
  }
  .ng-table-group-close,
  .ng-table-group-toggle {
    float: right;
  }
  .ng-table-group-toggle {
    margin-right: 5px;
  }
}

@media only screen and (max-width: 800px) {
  .ng-table-responsive {
    border-bottom: 1px solid $table-border-color;
    tr {
      border-top: 1px solid $table-border-color;
      border-left: 1px solid $table-border-color;
      border-right: 1px solid $table-border-color;
    }
    td:before {
      position: absolute;
      padding: 8px;
      left: 0;
      top: 0;
      width: 50%;
      white-space: nowrap;
      text-align: left;
      font-weight: bold;
    }
    thead {
      tr {
        th {
          text-align: left;
        }
        &.ng-table-filters {
          th {
            padding: 0;
            form>div {
              padding: 8px;
            }
          }
        }
      }
    }
    td {
      border: none;
      border-bottom: 1px solid $cell-border-color;
      position: relative;
      padding-left: 50%;
      white-space: normal;
      text-align: left;
      &:before {
        content: attr(data-title-text);
      }
    }
    &,
    thead,
    tbody,
    th,
    td,
    tr {
      display: block;
    }
  }
  .ng-table-pager {}
  .ng-table-counts {}
}

.ng-table-pagination {
  margin-top: 0;
}

.ng-table-group-selector:before,
.ng-table-group-selector:after,
.filter:before,
.filter:after {
  display: table;
  content: " ";
}

.ng-table-group-selector:after,
.filter:after {
  clear: both;
}

.filter>.filter-cell {
  float: left;
  box-sizing: border-box;
}

.filter-horizontal>.filter-cell {
  padding: 0 2px;
}

.filter-horizontal>.filter-cell:first-child {
  padding-left: 0;
}

.filter-horizontal>.filter-cell {
  &:last-child,
  &.last {
    padding-right: 0;
  }
}

.s12 {
  width: 100%;
}

.s11 {
  width: 91.66666666666666%;
}

.s10 {
  width: 83.33333333333334%;
}

.s9 {
  width: 75%;
}

.s8 {
  width: 66.66666666666666%;
}

.s7 {
  width: 58.333333333333336%;
}

.s6 {
  width: 50%;
}

.s5 {
  width: 41.66666666666667%;
}

.s4 {
  width: 33.33333333333333%;
}

.s3 {
  width: 25%;
}

.s2 {
  width: 16.666666666666664%;
}

.s1 {
  width: 8.333333333333332%;
}

@media all and (max-width: 468px) {
  .s12,
  .s11,
  .s10,
  .s9,
  .s8,
  .s7,
  .s6,
  .s5,
  .s4,
  .s3,
  .s2,
  .s1 {
    width: 100%;
  }
  .filter>.filter-cell {
    padding: 0px;
  }
}



